import React from 'react'

import Select from 'antd/es/select'
import UserButton from '../UserButton/UserButton'

export default (account, friendData, value?, onSelect?) => (
    <Select
        mode="multiple"
        style={{ width: '100%' }}
        placeholder="Please select"
        value={value}
        onChange={onSelect}
        getPopupContainer={trigger => trigger.parentNode}
    >
        {[
            account,
            ...account.friends
                .filter(friend => friend.status === 'friend')
                .map(friend => friendData[friend.friend]),
        ].map(item => (
            <Select.Option key={item._id}>
                <UserButton id={item._id} noClick={true} />
            </Select.Option>
        ))}
    </Select>
)
