import React from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import Card from 'antd/es/card'
import Button from 'antd/es/button'
import UserButton from '../../components/UserButton/UserButton'
import MasonryGrid from '../../components/MasonryGrid'

const Wallet = props => {
    const account = props.pulledProfile || props.account
    return account.wallet ? (
        <div>
            <MasonryGrid>
                {account.wallet.map((money, index) => (
                    <div
                        onClick={() =>
                            props.history.push('/profile/' + money.user)
                        }
                        key={index}
                    >
                        <Card
                            type="inner"
                            style={{ width: '100%' }}
                            bordered={false}
                            extra={<Button type="primary">Visit shop</Button>}
                            title={
                                <div>
                                    <span style={{ fontWeight: 'bold' }}>
                                        ${money.amount}
                                    </span>
                                    <UserButton id={money.user} />
                                </div>
                            }
                        />
                    </div>
                ))}
            </MasonryGrid>
        </div>
    ) : null
}
const mapStateToProps = state => {
    return {
        account: state.account,
        isAuthed: state.isAuthed,
    }
}

export default withRouter(connect(mapStateToProps)(Wallet))
