import React from 'react'

import * as wsActions from '../../websocketActions'
import EditGoalAndProgressForm from '../../components/EditGoalAndProgressForm/EditGoalAndProgressForm'

const EditProgress = props => {
    const { progress, account, state, setState, friendData } = props

    const milestonesValues = {}
    for (let milestone of progress.goal.milestonesValues) {
        milestonesValues[milestone.key] = milestone
    }
    const goal = { ...progress.goal, ...milestonesValues }

    const onFinish = value => {
        wsActions.editGoalInProgress(props, value)
        setState({ ...state, editProgressOpen: false })
    }

    return goal ? (
        <EditGoalAndProgressForm
            goal={goal}
            onFinish={onFinish}
            account={account}
            friendData={friendData}
        />
    ) : null
}

export default EditProgress
