import React, { useState } from 'react'
import { withRouter } from 'react-router-dom'
import * as wsActions from '../../websocketActions'
import Message from './Message'
import { LikeOutlined, LikeFilled } from '@ant-design/icons'
import AddMessage from './AddMessage'
import Divider from 'antd/es/divider'
import Button from 'antd/es/button'
import ImageCarousel from '../UI/ImageCarousel/ImageCarousel'

const Post = props => {
    const { post, account, friendData, progressData, groupData } = props
    const [postOpen, setPostOpen] = useState(props.showReplies)
    const getNumberOfMessages = messages => {
        let result = 0
        for (let item of messages) {
            result = result + 1 + item.replies.length
        }
        return result
    }
    return post ? (
        <>
            {!props.noStartMessage && (
                <>
                    <Message
                        message={post.startMessage}
                        post={post}
                        account={account}
                        friendData={friendData}
                        progressData={progressData}
                        groupData={groupData}
                        replies={[]}
                        isStart={true}
                    />
                    {post.startMessage.image &&
                        post.startMessage.image.length > 0 && (
                            <ImageCarousel
                                images={post.startMessage.image}
                                height="60vmin"
                                type="contain"
                            />
                        )}
                </>
            )}
            <Divider type="horizontal" style={{ margin: '5px 0' }} />

            <div
                style={{
                    textAlign: 'left',
                    // marginBottom: '200px',
                    position: 'relative',
                }}
            >
                <div
                    style={{ display: 'flex', justifyContent: 'space-around' }}
                >
                    <span key="comment-basic-like">
                        <Button type="text">
                            {post.startMessage.likes.includes(account._id) ? (
                                <LikeFilled
                                    onClick={() =>
                                        wsActions.changeLikesMessage(
                                            'dislikeMessage',
                                            post.startMessage.messageId,
                                            account._id,
                                            post._id
                                        )
                                    }
                                />
                            ) : (
                                <LikeOutlined
                                    onClick={() =>
                                        wsActions.changeLikesMessage(
                                            'likeMessage',
                                            post.startMessage.messageId,
                                            account._id,
                                            post._id
                                        )
                                    }
                                />
                            )}

                            <span style={{ paddingLeft: '8px' }}>
                                {post.startMessage.likes.length}
                            </span>
                        </Button>
                    </span>
                    <span>
                        <Button onClick={() => setPostOpen(true)} type="text">
                            Comment
                            <span style={{ paddingLeft: '8px' }}>
                                {getNumberOfMessages(post.messages)}
                            </span>
                        </Button>
                    </span>
                </div>
                <Divider type="horizontal" style={{ margin: '5px 0' }} />
                {postOpen ? (
                    <>
                        <AddMessage post={post} account={account} />
                        {post.messages && post.messages.length
                            ? post.messages.map((message, index) => (
                                  <Message
                                      key={index}
                                      message={message}
                                      post={post}
                                      account={account}
                                      friendData={friendData}
                                      replies={message.replies}
                                      isTop={true}
                                  />
                              ))
                            : null}
                    </>
                ) : (
                    <Button onClick={() => setPostOpen(true)} type="text">
                        Show replies and comment
                    </Button>
                )}
            </div>
        </>
    ) : null
}

export default withRouter(Post)
