import React, { useState } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import Tabs from 'antd/es/tabs'
import Button from 'antd/es/button'
import Empty from 'antd/es/empty'
import ProgressCard from './ProgressCard'
import AddCard from '../../components/AddCard/AddCard'
import Modal from 'antd/es/modal/Modal'
import AddProgress from './AddProgress'
import GroupCard from './GroupCard'
import Masonry from 'react-masonry-css'
import { cardBreakPoints } from '../../utils/masonryBreakPoints'
import MasonryGrid from '../../components/MasonryGrid'
import MasonryCard from '../../components/MasonryCard'
import UserButton from '../../components/UserButton/UserButton'

const { TabPane } = Tabs

const Progresses = props => {
    const { fromProfile, progressData, account, groupData, profile } = props

    const { progresses, groups } = fromProfile ? profile : account
    const [startProgressVisible, setStartProgressVisible] = useState(false)
    if (!fromProfile) props.setPageTitle({ main: 'Progresses', sub: '' })
    let loadedProgresses, loadedGroups
    if (progresses) {
        loadedProgresses = progresses
            .filter(item => progressData[item])
            .map(item => progressData[item])
        loadedGroups = groups
            .filter(item => groupData[item] && groupData[item].active)
            .map(item => groupData[item])
    }

    const editGoal = fromProfile ? null : loadedProgresses && loadedGroups ? (
        <Modal
            title="Add progress"
            visible={startProgressVisible}
            maskClosable
            closable
            onCancel={() => setStartProgressVisible(false)}
            width="100%"
            style={{ maxWidth: '1000px' }}
            footer={
                <Button
                    key="back"
                    onClick={() => setStartProgressVisible(false)}
                >
                    Close
                </Button>
            }
            destroyOnClose={true}
        >
            <AddProgress
                setStartProgressVisible={setStartProgressVisible}
                account={account}
                friendData={props.friendData}
            />
        </Modal>
    ) : null

    const innerTabs =
        loadedProgresses && loadedGroups ? (
            <>
                <TabPane tab="Progresses" key="progresses">
                    {!fromProfile && (
                        <>
                            <AddCard
                                title="Start a new progress"
                                sub="Rich your goals or help others!"
                                action={() => {
                                    setStartProgressVisible(true)
                                }}
                            />
                            {editGoal}
                        </>
                    )}
                    <MasonryGrid>
                        {loadedProgresses.map((item, index) => (
                            <MasonryCard
                                key={index}
                                onClickLink={'/progresses/' + item._id}
                                images={item.goal.images}
                                body={
                                    <>
                                        <UserButton
                                            ids={item.goal.users}
                                            group={true}
                                        />
                                        <span style={{ fontWeight: 'bold' }}>
                                            {item.name}
                                        </span>
                                    </>
                                }
                            />
                        ))}
                    </MasonryGrid>
                </TabPane>
                <TabPane tab="Groups" key="groups">
                    {loadedGroups.length > 0 ? (
                        loadedGroups.map((group, index) => (
                            <GroupCard key={index} group={group} />
                        ))
                    ) : (
                        <Empty
                            imageStyle={{
                                height: 160,
                            }}
                            description={
                                <span>
                                    Here you will find your groups. Groups can
                                    be created in existing progresses.
                                </span>
                            }
                        />
                    )}
                </TabPane>
            </>
        ) : null

    return loadedProgresses && loadedGroups ? (
        // <Tabs
        //     style={{
        //         background: 'transparent',
        //         boxShadow: 'none',
        //         margin: '0',
        //         marginTop: '-15px',
        //     }}
        // >
        //     {innerTabs}
        // </Tabs>
        <div>
            {!fromProfile && (
                <>
                    <AddCard
                        title="Start a new progress"
                        sub="Rich your goals or help others!"
                        action={() => {
                            setStartProgressVisible(true)
                        }}
                    />
                    {editGoal}
                </>
            )}
            <MasonryGrid style={{ marginTop: '40px' }}>
                {loadedProgresses.map((item, index) => (
                    <MasonryCard
                        key={index}
                        onClickLink={'/progresses/' + item._id}
                        images={item.goal.images}
                        body={
                            <>
                                <UserButton
                                    ids={item.goal.users}
                                    group={true}
                                />
                                <span style={{ fontWeight: 'bold' }}>
                                    {item.name}
                                </span>
                            </>
                        }
                    />
                ))}
            </MasonryGrid>
        </div>
    ) : null

    // ) : (
    //     <Empty
    //         imageStyle={{
    //             height: 160,
    //         }}
    //         description={
    //             <span>
    //                 This user has no progresses. You can create a goal and
    //                 invite this user.
    //             </span>
    //         }
    //     />
    // )
}

const mapStateToProps = state => {
    return {
        account: state.account,
        profile: state.profile,
        isAuthed: state.isAuthed,
        progressData: state.progressData,
        groupData: state.groupData,
        friendData: state.friendData,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: (data, pulledFrom) =>
            dispatch(actions.setData(data, pulledFrom)),
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
        tryAuth: (props, forceAuth) =>
            dispatch(actions.tryAuth(props, forceAuth)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Progresses)
)
