import React from 'react'
import { withRouter } from 'react-router-dom'

import Card from 'antd/es/card'
import UserButton from '../../components/UserButton/UserButton'
import ImageCarousel from '../../components/UI/ImageCarousel/ImageCarousel'

const ProgressCard = props => {
    const { progress } = props
    const { goal } = progress
    return (
        <Card
            type="inner"
            style={{ width: '100%', cursor: 'pointer' }}
            bordered={false}
            title={
                <div
                    onClick={() =>
                        props.history.push('/progresses/' + progress._id)
                    }
                    style={{
                        display: 'flex',
                        alignItems: 'center',
                    }}
                >
                    {goal.images.length > 0 && (
                        <div style={{ width: '50px', marginRight: '20px' }}>
                            <ImageCarousel images={goal.images} size={50} />
                        </div>
                    )}
                    {goal.name} -{' '}
                    <UserButton id={progress.owner} noName={true} /> =&gt;{' '}
                    <UserButton ids={progress.goal.users} group={true} />
                </div>
            }
        />
    )
}

export default withRouter(ProgressCard)
