import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import Avatar from 'antd/es/avatar'
import Card from 'antd/es/card'

import friendActionButton from '../../components/FriendActionButton'
import FriendCard from '../../components/FriendCard'
import Tabs from 'antd/es/tabs'
import Progresses from '../Progresses/Progresses'
import Wallet from '../Shop/Wallet'
import PerkCard from '../Shop/PerkCard'
import getAvatar from '../../utils/getAvatar'
import pullData from '../../utils/pullData'
import UserButton from '../../components/UserButton/UserButton'
import MasonryGrid from '../../components/MasonryGrid'

const Profile = props => {
    const [data, setData] = useState({})
    const { profile, friendData, account } = props.isAuthed ? props : data

    useEffect(() => {
        const pathParts = props.history.location.pathname.split('/')
        if (pathParts.length > 2) {
            const profileId = pathParts[2]
            if (props.isAuthed) {
                props.setData({ currentProfile: profileId })
            } else {
                pullData(props, data, setData, { forceAuth: false }, data => {
                    const friendData = {}
                    for (let friend of data.friendData)
                        friendData[friend._id] = friend
                    const progressData = {}
                    for (let progress of data.progressData)
                        progressData[progress._id] = progress
                    const groupData = {}
                    for (let group of data.groupData)
                        groupData[group._id] = group

                    props.setData({
                        // profile: data.profile,
                        friendData,
                        progressData,
                        groupData,
                        account: {},
                    })
                })
            }
        }
    }, [props.isAuthed, props.history.location.pathname])

    if (!profile || !profile._id) return null
    props.setPageTitle({ main: profile.name, sub: '' })

    const realFriends = [],
        invitedFriends = [],
        invitingFriends = []
    if (profile && profile.friends && account && account._id) {
        for (let friend of account.friends) {
            if (friend.status === 'friend') realFriends.push(friend)
            else if (friend.status === 'invited') invitedFriends.push(friend)
            else if (friend.status === 'inviting') invitingFriends.push(friend)
        }
    }
    const friendActions = friendActionButton({
        accountId: account ? account._id : '',
        realFriends: realFriends,
        invitedFriends: invitedFriends,
        invitingFriends: invitingFriends,
        _id: profile._id,
        additionalButton: null,
        asText: true,
    })
    return (
        <div>
            <Card>
                <div style={{ textAlign: 'center', marginBottom: '50px' }}>
                    <Avatar
                        src={getAvatar(profile._id, profile.image)}
                        size={200}
                    />
                    <h1>{profile.name}</h1>
                    <h3>({profile._id})</h3>
                    {friendActions}
                </div>
                <Tabs>
                    <Tabs.TabPane tab="Progresses" key={0}>
                        <Progresses fromProfile={true} />
                    </Tabs.TabPane>

                    <Tabs.TabPane tab="Friends" key={1}>
                        <MasonryGrid friends={true}>
                            {profile.friends
                                .filter(friend => friend.status === 'friend')
                                .map(friend => {
                                    const oneFriendData = {
                                        ...friendData[friend.friend],
                                        ...friend.friend,
                                    }

                                    return (
                                        <FriendCard
                                            key={oneFriendData._id}
                                            {...props}
                                            account={account}
                                            friend={oneFriendData}
                                            realFriends={realFriends}
                                            invitingFriends={invitingFriends}
                                            invitedFriends={invitedFriends}
                                        />
                                    )
                                })}
                        </MasonryGrid>
                    </Tabs.TabPane>
                    {/* <Tabs.TabPane tab="Goals" key={3}>
                        {profile.goals.map((goal, index) => (
                            <PerkCard
                                key={index}
                                perk={goal}
                                perkId={goal.itemId}
                                owner={profile}
                                account={account || {}}
                                setEditedPerk={() => {}}
                                setEditPerkOpen={() => {}}
                                onlyBuy={true}
                                mode="goals"
                            />
                        ))}
                    </Tabs.TabPane> */}
                    <Tabs.TabPane tab="Shop" key={4}>
                        <MasonryGrid>
                            {profile.perks &&
                                profile.perks.map((perk, index) => (
                                    <PerkCard
                                        key={index}
                                        perk={perk}
                                        perkId={perk.perkId}
                                        owner={profile}
                                        account={account || {}}
                                        onlyBuy={true}
                                        mode="shop"
                                    />
                                ))}
                        </MasonryGrid>
                    </Tabs.TabPane>
                    <Tabs.TabPane tab="Wallet" key={5}>
                        <Wallet pulledProfile={profile} account={profile} />
                    </Tabs.TabPane>
                    <Tabs.TabPane tab="Wishlist" key={6}>
                        <MasonryGrid>
                            {profile.wishlist.map((item, index) => (
                                <PerkCard
                                    key={index}
                                    perk={item}
                                    perkId={item.itemId}
                                    owner={profile}
                                    account={account || {}}
                                    setEditedPerk={() => {}}
                                    setEditPerkOpen={() => {}}
                                    onlyBuy={true}
                                    mode="wishlist"
                                />
                            ))}
                        </MasonryGrid>
                    </Tabs.TabPane>
                </Tabs>
            </Card>
        </div>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
        profile: state.profile,
        isAuthed: state.isAuthed,
        friendData: state.friendData,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => dispatch(actions.setData(data)),
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
        tryAuth: (props, forceAuth) =>
            dispatch(actions.tryAuth(props, forceAuth)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Profile)
)
