import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'

import Modal from 'antd/es/modal'
import Activity from './Activity'
import Card from 'antd/es/card'
import Post from '../../components/Post/Post'
import AddPost from '../../components/Post/AddPost'
import Tabs from 'antd/es/tabs'
import pullData from '../../utils/pullData'

const Progress = props => {
    const [state, setState] = useState({})

    const [data, setData] = useState({})
    const pathParts = props.history.location.pathname.split('/')
    const progressId = pathParts.length > 2 ? pathParts[2] : ''
    if (!progressId) return null
    useEffect(() => {
        if (props.isAuthed) {
            props.setData({ currentProgress: progressId })
        } else {
            pullData(props, data, setData, { forceAuth: false }, data => {
                const friendData = {}
                for (let friend of data.friendData)
                    friendData[friend._id] = friend
                const post = {}
                for (let item of data.post) post[item._id] = item

                props.setData({
                    friendData,
                    posts: post,
                    account: {},
                    progress: data.progress,
                })
            })
        }
    }, [props.isAuthed, props.history.location.pathname])

    const { progress, account, friendData, posts } = props.isAuthed
        ? props
        : { ...data, account: {} }

    if (!progress || !progress._id) return null
    props.setData({ currentPosts: progress.posts })

    const { goal } = progress
    if (goal) props.setPageTitle({ main: goal.name, sub: progress.worker })
    return goal && friendData ? (
        <div className="contentDiv">
            <Tabs>
                <Tabs.TabPane tab="Details" key="details">
                    <Activity
                        state={state}
                        setState={setState}
                        progress={progress}
                        account={account}
                        friendData={friendData}
                    />
                </Tabs.TabPane>
                <Tabs.TabPane tab="Posts" key="posts">
                    {account._id && (
                        <AddPost
                            parent={progressId}
                            account={account}
                            parentType="progress"
                            // onSend={() => setEditingMessage('')}
                        />
                    )}
                    {progress.posts.map(postId => {
                        if (posts[postId]) {
                            const post = posts[postId]
                            return (
                                <div key={postId}>
                                    <div
                                        style={{
                                            height: '5px',
                                            background: '#ddd',
                                            width: 'calc(100% + 8px)',
                                            position: 'absolute',
                                            left: '-8px',
                                        }}
                                    />
                                    <div style={{ paddingTop: '10px' }}>
                                        <Post
                                            friendData={friendData}
                                            account={account}
                                            post={post}
                                        />
                                    </div>
                                </div>
                            )
                        } else return null
                    })}
                </Tabs.TabPane>
            </Tabs>
            <Modal
                visible={state.previewImage}
                footer={null}
                onCancel={() => setState({ ...state, previewImage: '' })}
            >
                <img
                    alt="example"
                    style={{ width: '100%' }}
                    src={state.previewImage}
                />
            </Modal>
        </div>
    ) : null
}
const mapStateToProps = state => {
    return {
        isAuthed: state.isAuthed,
        account: state.account,
        posts: state.post,
        progress: state.progress,
        friendData: state.friendData,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => {
            dispatch(actions.setData(data))
        },
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Progress)
)
