import React from 'react'
import Checkbox from 'antd/es/checkbox'
import Descriptions from 'antd/es/descriptions'

import * as wsActions from '../../websocketActions'
import Rewards from '../../components/Rewards/Rewards'
import UserButton from '../../components/UserButton/UserButton'
import Collapse from 'antd/es/collapse'
import Badge from 'antd/es/badge'
import Typography from 'antd/es/typography'
import Divider from 'antd/es/divider'

const ActivityTable = props => {
    const { progress, account } = props
    const { goal } = progress
    const { rewardsGroups } = goal

    const renderCheckbox = ({ checked, milestoneId, disabled }) => (
        <Checkbox
            checked={checked}
            onChange={e =>
                wsActions.changeStage(props, e.target.checked, milestoneId)
            }
            disabled={disabled}
        />
    )

    const getCheckboxStatus = (id, stage, milestone) => {
        return {
            checked: stage.approvedBy.find(item => item.accountId === id),
            milestoneId: milestone.key,
            disabled: id !== account._id,
        }
    }

    return (
        <Collapse
            expandIconPosition="right"
            style={{ marginBottom: '50px' }}
            destroyInactivePanel={true}
            accordion
        >
            {goal.milestonesValues.map(milestone => {
                const stage = progress.stages.find(
                    stage => stage.milestoneId === milestone.key
                ) || { approvedBy: [] }

                const experts = goal.experts.map(expert => (
                    <Descriptions.Item
                        key={expert}
                        label={
                            <>
                                <UserButton
                                    id={expert}
                                    noAva={true}
                                    span={true}
                                />{' '}
                                {' (expert)'}
                            </>
                        }
                    >
                        {renderCheckbox(
                            getCheckboxStatus(expert, stage, milestone)
                        )}
                    </Descriptions.Item>
                ))

                const users = goal.users.map(user => (
                    <Descriptions.Item
                        key={user}
                        label={
                            <>
                                <UserButton
                                    id={user}
                                    noAva={true}
                                    span={true}
                                />{' '}
                                {' (achiever)'}
                            </>
                        }
                    >
                        {renderCheckbox(
                            getCheckboxStatus(user, stage, milestone)
                        )}
                    </Descriptions.Item>
                ))

                const owner = [
                    <Descriptions.Item
                        key={progress.owner}
                        label={
                            <>
                                <UserButton
                                    id={progress.owner}
                                    noAva={true}
                                    span={true}
                                />{' '}
                                {' (owner)'}
                            </>
                        }
                    >
                        {renderCheckbox(
                            getCheckboxStatus(progress.owner, stage, milestone)
                        )}
                    </Descriptions.Item>,
                ]
                return (
                    <Collapse.Panel
                        header={
                            <Badge
                                color={stage.status ? 'green' : 'blue'}
                                text={milestone.name}
                            />
                        }
                        key={milestone.key}

                        // extra={}
                    >
                        <Divider style={{ marginTop: '0px', fontSize: '15px' }}>
                            Status
                        </Divider>

                        <Descriptions
                            bordered
                            column={{
                                xxl: 4,
                                xl: 3,
                                lg: 3,
                                md: 3,
                                sm: 2,
                                xs: 1,
                            }}
                        >
                            <Descriptions.Item label="Current status">
                                {stage.status ? (
                                    <Badge
                                        status="success"
                                        text="Is complete"
                                    />
                                ) : (
                                    <Badge
                                        status="processing"
                                        text="In process"
                                    />
                                )}
                            </Descriptions.Item>
                            {users}
                            {owner}
                            {experts}
                        </Descriptions>
                        {milestone.description && (
                            <>
                                <Divider style={{ fontSize: '15px' }}>
                                    Description
                                </Divider>
                                <Typography.Paragraph
                                    ellipsis={{
                                        rows: 3,
                                        expandable: true,
                                        symbol: 'more',
                                    }}
                                    style={{ whiteSpace: 'pre-wrap' }}
                                >
                                    {milestone.description}
                                </Typography.Paragraph>
                            </>
                        )}

                        {stage.status && (
                            <>
                                <Divider style={{ fontSize: '15px' }}>
                                    Paid rewards
                                </Divider>

                                <Rewards
                                    rewardKey={1}
                                    rewards={[{ rewards: stage.paid, key: 1 }]}
                                    readOnly={true}
                                />
                            </>
                        )}
                        <Divider style={{ fontSize: '15px' }}>Rewards</Divider>
                        <Rewards
                            rewardKey={milestone.key}
                            rewards={rewardsGroups}
                        />
                    </Collapse.Panel>
                )
            })}
        </Collapse>
    )
}
export default ActivityTable
