import React from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'

import Card from 'antd/es/card'
import Button from 'antd/es/button'
import UserButton from '../../components/UserButton/UserButton'

const DashboardEvents = props => {
    const { lastNotifications } = props
    const getRewardText = item =>
        item.details.reward.money
            ? '$' + item.details.reward.money
            : item.details.reward.simple || item.details.reward.itemName
    return lastNotifications.map(item => {
        let text = null

        switch (item.code) {
            case 'change name':
                text = (
                    <span>
                        {' '}
                        <UserButton id={item.user} /> changed name.
                    </span>
                )
                break
            case 'delete account':
                text = (
                    <span>
                        {item.user}
                        deleted account.
                    </span>
                )
                break
            case 'friend request':
                text = (
                    <span>
                        <UserButton id={item.user} /> wants to become friends
                        with you.
                    </span>
                )
                break
            case 'friend add':
                text = (
                    <span>
                        <UserButton id={item.user} /> and{' '}
                        <UserButton id={item.details.friend} /> became friends.
                    </span>
                )
                break
            case 'unfriend':
                text = (
                    <span>
                        <UserButton id={item.user} /> and{' '}
                        <UserButton id={item.details.friend} /> stopped being
                        friends.
                    </span>
                )
                break
            case 'add wishlist item':
            case 'edit wishlist item':
                text = (
                    <span>
                        <UserButton id={item.user} />{' '}
                        {item.code === 'add wishlist item'
                            ? 'added to wishlist a new item'
                            : 'edited in wishlist'}{' '}
                        <Button
                            type="link"
                            onClick={() =>
                                props.history.push(
                                    `/wishlist/${item.user}/${
                                        item.details.itemId
                                    }`
                                )
                            }
                        >
                            {item.details.itemName}
                        </Button>{' '}
                        .
                    </span>
                )
                break
            case 'delete wishlist item':
                text = (
                    <span>
                        <UserButton id={item.user} /> deleted{' '}
                        {item.details.itemName} from wishlist.
                    </span>
                )
                break
            case 'add goal':
            case 'edit goal':
                text = (
                    <span>
                        <UserButton id={item.user} />{' '}
                        {item.code === 'add goal'
                            ? 'added a new goal'
                            : 'edited goal'}{' '}
                        <Button
                            type="link"
                            onClick={() =>
                                props.history.push(
                                    `/goals/${item.user}/${item.details.itemId}`
                                )
                            }
                        >
                            {item.details.itemName}
                        </Button>{' '}
                        .
                    </span>
                )
                break
            case 'delete goal':
                text = (
                    <span>
                        <UserButton id={item.user} /> deleted{' '}
                        {item.details.itemName} from goals.
                    </span>
                )
                break
            case 'add perk':
            case 'edit perk':
                text = (
                    <span>
                        <UserButton id={item.user} />{' '}
                        {item.code === 'add perk'
                            ? 'added to shop a new item'
                            : 'edited in shop'}{' '}
                        <Button
                            type="link"
                            onClick={() =>
                                props.history.push(
                                    `/shop/${item.user}/${item.details.itemId}`
                                )
                            }
                        >
                            {item.details.itemName}
                        </Button>{' '}
                        .
                    </span>
                )
                break
            case 'delete perk':
                text = (
                    <span>
                        <UserButton id={item.user} /> deleted{' '}
                        {item.details.itemName} from shop.
                    </span>
                )
                break
            case 'buy perk':
                text = (
                    <span>
                        <UserButton id={item.user} /> bought
                        <Button
                            type="link"
                            onClick={() =>
                                props.history.push(
                                    `/shop/${item.user}/${item.details.itemId}`
                                )
                            }
                        >
                            {item.details.itemName} (${item.details.price})
                        </Button>{' '}
                        from <UserButton id={item.details.owner} /> .
                    </span>
                )
                break
            case 'cancel transaction':
            case 'confirm transaction':
                text = (
                    <span>
                        <UserButton id={item.user} />{' '}
                        {item.code === 'cancel transaction'
                            ? 'cancelled'
                            : 'confirmed'}
                        purchase of
                        <Button
                            type="link"
                            onClick={() =>
                                props.history.push(
                                    `/shop/${item.user}/${item.details.itemId}`
                                )
                            }
                        >
                            {item.details.itemName} (${item.details.price})
                        </Button>{' '}
                        by <UserButton id={item.details.buyer} /> .
                    </span>
                )
                break
            case 'start progress':
                text = (
                    <span>
                        <UserButton id={item.user} /> started participating in
                        progress
                        <Button
                            type="link"
                            onClick={() =>
                                props.history.push(
                                    `/progresses/${item.details.itemId}`
                                )
                            }
                        >
                            {item.details.itemName}
                        </Button>{' '}
                        as {item.details.as}.
                    </span>
                )
                break
            case 'approve milestone':
                text = (
                    <span>
                        <UserButton id={item.user} /> approved milestone{' '}
                        {item.details.itemName} in
                        <Button
                            type="link"
                            onClick={() =>
                                props.history.push(
                                    `/progresses/${item.details.progressId}`
                                )
                            }
                        >
                            {item.details.progressName}
                        </Button>
                        .
                    </span>
                )
                break
            case 'disapprove milestone':
                text = (
                    <span>
                        <UserButton id={item.user} /> disapproved milestone{' '}
                        {item.details.itemName} in
                        <Button
                            type="link"
                            onClick={() =>
                                props.history.push(
                                    `/progresses/${item.details.progressId}`
                                )
                            }
                        >
                            {item.details.progressName}
                        </Button>
                        .
                    </span>
                )
                break
            case 'finish progress':
                text = (
                    <span>
                        Progress{' '}
                        <Button
                            type="link"
                            onClick={() =>
                                props.history.push(
                                    `/progresses/${item.details.progressId}`
                                )
                            }
                        >
                            {item.details.progressName}
                        </Button>
                        was approved and finished.
                    </span>
                )
                break
            case 'milestone finish':
                text = (
                    <span>
                        Milestone {item.details.itemName} in progress{' '}
                        <Button
                            type="link"
                            onClick={() =>
                                props.history.push(
                                    `/progresses/${item.details.progressId}`
                                )
                            }
                        >
                            {item.details.progressName}
                        </Button>
                        was approved and finished.
                    </span>
                )
                break
            case 'get reward':
                text = (
                    <span>
                        <UserButton id={item.user} /> recieved{' '}
                        {getRewardText(item)} as a reward from{' '}
                        <UserButton id={item.details.from} /> in progress
                        <Button
                            type="link"
                            onClick={() =>
                                props.history.push(
                                    `/progresses/${item.details.progressId}`
                                )
                            }
                        >
                            {item.details.progressName}
                        </Button>
                        .
                    </span>
                )
                break
            case 'leave progress':
                text = (
                    <span>
                        <UserButton id={item.user} /> left progress{' '}
                        <Button
                            type="link"
                            onClick={() =>
                                props.history.push(
                                    `/progresses/${item.details.progressId}`
                                )
                            }
                        >
                            {item.details.progressName}
                        </Button>
                        .
                    </span>
                )
                break
            case 'edit progress':
                text = (
                    <span>
                        <UserButton id={item.user} /> edited progress{' '}
                        <Button
                            type="link"
                            onClick={() =>
                                props.history.push(
                                    `/progresses/${item.details.progressId}`
                                )
                            }
                        >
                            {item.details.progressName}
                        </Button>
                        {item.details.addedAccounts.length > 0 ? (
                            <>
                                {' '}
                                and added{' '}
                                {item.details.addedAccounts.map(ac => (
                                    <UserButton id={ac} />
                                ))}{' '}
                            </>
                        ) : (
                            ''
                        )}
                        {item.details.droppedAccounts.length > 0 ? (
                            <>
                                {' '}
                                and removed{' '}
                                {item.details.droppedAccounts.map(ac => (
                                    <UserButton id={ac} />
                                ))}{' '}
                            </>
                        ) : (
                            ''
                        )}
                        .
                    </span>
                )
                break
            case 'remove from progress':
                text = (
                    <span>
                        <UserButton id={item.user} /> removed{' '}
                        <UserButton id={item.details.account} /> from progress
                        <Button
                            type="link"
                            onClick={() =>
                                props.history.push(
                                    `/progresses/${item.details.progressId}`
                                )
                            }
                        >
                            {item.details.progressName}
                        </Button>
                        .
                    </span>
                )
                break
            case 'add to progress':
                text = (
                    <span>
                        <UserButton id={item.user} /> added{' '}
                        <UserButton id={item.details.account} /> to progress
                        <Button
                            type="link"
                            onClick={() =>
                                props.history.push(
                                    `/progresses/${item.details.progressId}`
                                )
                            }
                        >
                            {item.details.progressName}
                        </Button>
                        .
                    </span>
                )
                break
            case 'add reward':
                text = (
                    <span>
                        <UserButton id={item.user} /> added{' '}
                        {getRewardText(item)} as a reward for
                        {!item.details.for || item.details.for.length === 0
                            ? 'all '
                            : item.details.for.map(account => (
                                  <>
                                      <UserButton id={account} />,
                                  </>
                              ))}
                        in progress
                        <Button
                            type="link"
                            onClick={() =>
                                props.history.push(
                                    `/progresses/${item.details.progressId}`
                                )
                            }
                        >
                            {item.details.progressName}
                        </Button>
                        .
                    </span>
                )
                break
            case 'delete reward':
                text = (
                    <span>
                        <UserButton id={item.user} /> removed{' '}
                        {getRewardText(item)} as a reward for{' '}
                        {!item.details.for || item.details.for.length === 0
                            ? 'all '
                            : item.details.for.map(account => (
                                  <>
                                      <UserButton id={account} />,
                                  </>
                              ))}
                        in progress
                        <Button
                            type="link"
                            onClick={() =>
                                props.history.push(
                                    `/progresses/${item.details.progressId}`
                                )
                            }
                        >
                            {item.details.progressName}
                        </Button>
                        .
                    </span>
                )
                break
            default:
                console.log(item)
                text = 'New event'
                break
        }
        return <Card>{text}</Card>
    })
}
const mapStateToProps = state => {
    return {
        lastNotifications: state.lastNotifications,
    }
}

export default withRouter(connect(mapStateToProps)(DashboardEvents))
