import React, { useState } from 'react'
import Modal from 'antd/es/modal'

import Button from 'antd/es/button'
import Input from 'antd/es/input'
import Form from 'antd/es/form'
import Collapse from 'antd/es/collapse'
import Divider from 'antd/es/divider'
import Upload from 'antd/es/upload'
import PlusOutlined from '@ant-design/icons/PlusOutlined'

import { uploadFile } from '../../utils/uploadFile'
import Rewards from '../Rewards/Rewards'
import { nameRules, descriptionRules } from '../../constants/fieldRules'
import connectionsSelect from './connectionsSelect'
import Dropdown from 'antd/es/dropdown/dropdown'
import { sisOutlined } from '@ant-design/icons'
import Menu from 'antd/es/menu'
import MoreIcon from '../UI/MoreIcon'

const EditGoalAndProgressForm = props => {
    const { goal, account, friendData } = props

    const [milestones, setMilestones] = useState(
        (goal && goal.milestones) || [
            { name: 'Start', key: 'start' },
            { name: 'Finish', key: 'finish' },
        ]
    )

    const [images, setImages] = useState(
        (goal &&
            goal.images.map((url, index) => ({
                url,
                uid: index,
            }))) ||
            []
    )
    const [withMilestones, setWithMilestones] = useState(
        goal && goal.withMilestones
    )
    const [previewImage, setPreviewImage] = useState('')
    const [newTabIndex, setNewTabIndex] = useState(
        (goal && goal.newTabIndex) || 2
    )

    const [rewards, setRewards] = useState((goal && goal.rewardsGroups) || [])

    const onFinish = value => {
        const milestonesValues = []
        for (let milestone of milestones) {
            milestonesValues.push({
                ...value[milestone.key],
                key: milestone.key,
                name: milestone.name,
            })
        }

        props.onFinish({
            ...value,
            images: images.map(image => image.url),
            milestones,
            milestonesValues,
            newTabIndex,
            rewardsGroups: rewards,
        })
    }

    const addMilestone = milestone => {
        const newPanes = [...milestones]
        const newKey = `mile${newTabIndex}`
        if (typeof milestone !== 'undefined') {
            const indexOfMile = milestones.findIndex(
                mile => mile.key === milestone
            )
            if (indexOfMile < 0) return
            newPanes.splice(indexOfMile + 1, 0, {
                key: newKey,
                name: 'New milestone',
            })
        } else {
            if (newPanes.find(item => item.key === 'finish'))
                newPanes.splice(newPanes.length - 1, 0, {
                    key: newKey,
                    name: 'New milestone',
                })
            else
                newPanes.push({
                    key: newKey,
                    name: 'New milestone',
                })
        }
        setMilestones(newPanes)
        setNewTabIndex(newTabIndex + 1)
        form.setFieldsValue({
            [newKey]: { key: newKey, name: 'New milestone' },
        })
    }

    const layout = {
        labelCol: { span: 8 },
        wrapperCol: { span: 16 },
    }
    const tailLayout = {
        wrapperCol: { offset: 8, span: 16 },
    }

    const [form] = Form.useForm()

    const normFile = e => {
        if (Array.isArray(e)) {
            return e
        }
        return e && e.fileList.map(image => image.url)
    }

    const connections = connectionsSelect(account, friendData)

    const MilestoneControls = props => {
        const moveMilestoneUp = () => {
            const indexOfMile = milestones.findIndex(
                mile => mile.key === props.milestone
            )
            if (indexOfMile <= 0) return
            const newMilestones = [...milestones]
            newMilestones.splice(
                indexOfMile - 1,
                0,
                newMilestones.splice(indexOfMile, 1)[0]
            )
            setMilestones(newMilestones)
        }

        const moveMilestoneDown = () => {
            const indexOfMile = milestones.findIndex(
                mile => mile.key === props.milestone
            )
            if (indexOfMile < 0 || indexOfMile >= milestones.length - 1) return
            const newMilestones = [...milestones]
            newMilestones.splice(
                indexOfMile + 1,
                0,
                newMilestones.splice(indexOfMile, 1)[0]
            )
            setMilestones(newMilestones)
        }

        const addMilestoneBelow = () => {
            addMilestone(props.milestone)
        }

        const deleteMilestone = () => {
            const newPanes = milestones.filter(
                pane => pane.key !== props.milestone
            )
            setMilestones(newPanes)
        }

        return (
            <Dropdown
                overlay={
                    <Menu
                        onClick={({ domEvent }) => domEvent.stopPropagation()}
                    >
                        {!props.finish && (
                            <Menu.Item key="0" onClick={addMilestoneBelow}>
                                Add a milestone below
                            </Menu.Item>
                        )}
                        {!props.finish && !props.start && (
                            <Menu.Item key="1" onClick={moveMilestoneUp}>
                                Move up
                            </Menu.Item>
                        )}
                        {!props.finish && !props.start && (
                            <Menu.Item key="2" onClick={moveMilestoneDown}>
                                Move down
                            </Menu.Item>
                        )}
                        <Menu.Divider />
                        <Menu.Item
                            key="3"
                            danger={true}
                            onClick={deleteMilestone}
                        >
                            Delete
                        </Menu.Item>
                    </Menu>
                }
                trigger={['click', 'hover']}
            >
                <a
                    className="ant-dropdown-link"
                    onClick={e => {
                        e.preventDefault()
                        e.stopPropagation()
                    }}
                >
                    <MoreIcon />
                </a>
            </Dropdown>
        )
    }
    return !props.goalId || goal ? (
        <>
            <Form
                form={form}
                name="dynamic_form_nest_item"
                onFinish={onFinish}
                autoComplete="off"
                initialValues={
                    goal || {
                        name: 'New achievement',
                        start: {
                            name: 'Start',
                            key: 'start',
                            description: 'This is the start of the progress.',
                        },
                        finish: {
                            name: 'Finish',
                            key: 'finish',
                            description: 'This is the finish of the progress.',
                        },
                    }
                }
                {...layout}
                onValuesChange={changedValues => {
                    for (let attr in changedValues) {
                        const index = milestones.findIndex(
                            item => item.key === attr
                        )

                        if (index > -1) {
                            if (changedValues[attr].name) {
                                const newMilestones = [...milestones]
                                newMilestones[index] = {
                                    ...newMilestones[index],
                                    name: changedValues[attr].name,
                                }
                                setMilestones(newMilestones)
                            }
                        }
                        if (attr === 'withMilestones') {
                            setWithMilestones(changedValues[attr])
                        }
                    }
                }}
            >
                <Form.Item
                    name="images"
                    label="Images"
                    valuePropName="images"
                    getValueFromEvent={normFile}
                >
                    <Upload
                        listType="picture-card"
                        fileList={images.map((image, index) => ({
                            ...image,
                            uid: index,
                        }))}
                        onPreview={file => {
                            setPreviewImage(file.url)
                        }}
                        onChange={({ fileList }) => setImages(fileList)}
                        customRequest={({ file }) => {
                            uploadFile(file, 800, undefined, url => {
                                setImages(
                                    [...images, { url }].map(
                                        (image, index) => ({
                                            ...image,
                                            uid: index,
                                        })
                                    )
                                )
                            })
                        }}
                    >
                        {images.length >= 8 ? null : (
                            <div>
                                <PlusOutlined />
                                <div className="ant-upload-text">Upload</div>
                            </div>
                        )}
                    </Upload>
                </Form.Item>
                <Form.Item label="Name" name="name" rules={nameRules}>
                    <Input />
                </Form.Item>
                <Form.Item
                    label="Description"
                    name="description"
                    rules={descriptionRules}
                >
                    <Input.TextArea
                        autoSize={{ minRows: 4, maxRows: 20 }}
                        placeholder="Write a few words about why this goal is important and how to achieve it."
                    />
                </Form.Item>

                <Form.Item label="Achievers" name="users">
                    {connections}
                </Form.Item>

                <Form.Item label="Experts" name="experts">
                    {connections}
                </Form.Item>
                <Form.Item label="Supporters" name="supporters">
                    {connections}
                </Form.Item>

                <Divider>Milestones</Divider>
                <Collapse expandIconPosition="right" accordion>
                    {[
                        ...(milestones.find(item => item.key === 'start')
                            ? [milestones.find(item => item.key === 'start')]
                            : []),
                        ...milestones.filter(
                            item =>
                                item.key !== 'start' && item.key !== 'finish'
                        ),
                        ...(milestones.find(item => item.key === 'finish')
                            ? [milestones.find(item => item.key === 'finish')]
                            : []),
                    ].map(milestone => {
                        return (
                            <Collapse.Panel
                                header={
                                    form.getFieldValue([
                                        milestone.key,
                                        'name',
                                    ]) || milestone.name
                                }
                                key={milestone.key}
                                extra={
                                    <MilestoneControls
                                        milestone={milestone.key}
                                        start={milestone.key === 'start'}
                                        finish={milestone.key === 'finish'}
                                    />
                                }
                            >
                                <Form.Item
                                    label="Name"
                                    name={[milestone.key, 'name']}
                                    rules={nameRules}
                                >
                                    <Input />
                                </Form.Item>
                                <Form.Item
                                    label="Description"
                                    name={[milestone.key, 'description']}
                                    rules={descriptionRules}
                                >
                                    <Input.TextArea placeholder="Write a few words about why this milestone is important and how to achieve it." />
                                </Form.Item>
                                <Rewards
                                    rewardKey={milestone.key}
                                    label="Rewards"
                                    rewards={rewards}
                                    setRewards={setRewards}
                                />
                            </Collapse.Panel>
                        )
                    })}
                </Collapse>
                <Button type="link" onClick={() => addMilestone()}>
                    <PlusOutlined /> Add a new milestone
                </Button>

                <Form.Item {...tailLayout}>
                    <Button type="primary" htmlType="submit" size="large">
                        Save
                    </Button>
                </Form.Item>
            </Form>
            <Modal
                visible={previewImage}
                footer={null}
                onCancel={() => setPreviewImage('')}
            >
                <img
                    alt="example"
                    style={{ width: '100%' }}
                    src={previewImage}
                />
            </Modal>
        </>
    ) : null
}

export default EditGoalAndProgressForm
