import React from 'react'
import { withRouter } from 'react-router-dom'

import Card from 'antd/es/card'

const { Meta } = Card

const GroupCard = props => {
    const { group } = props

    return (
        <div
            style={{
                display: 'inline-block',
                margin: '4px 8px',
            }}
            onClick={() => props.history.push('/groups/' + group._id)}
        >
            <Card
                type="inner"
                style={{
                    width: 200,
                }}
                hoverable
                cover={
                    <div
                        onClick={() =>
                            props.history.push('/groups/' + group._id)
                        }
                        style={{
                            backgroundImage:
                                'url(' +
                                (group.images &&
                                    group.images.length > 0 &&
                                    group.images[0]) +
                                ')',
                            height: 200,
                            width: 200,
                            backgroundSize: 'cover',
                        }}
                    />
                }
            >
                <Meta title={group.name} />
            </Card>
        </div>
    )
}

export default withRouter(GroupCard)
