import React from 'react'
import { withRouter } from 'react-router-dom'
import Button from 'antd/es/button'
import Divider from 'antd/es/divider'
import Modal from 'antd/es/modal'

import Descriptions from 'antd/es/descriptions'

const { Item } = Descriptions

import * as wsActions from '../../websocketActions'
import ActivityTable from './ActivityTable'
import EditProgress from './EditProgress'
import Popconfirm from 'antd/es/popconfirm'
import Typography from 'antd/es/typography'
import UserButton from '../../components/UserButton/UserButton'
import ImageCarousel from '../../components/UI/ImageCarousel/ImageCarousel'

const Activity = props => {
    const { account, progress, state, setState, friendData } = props
    const { goal } = progress
    return goal ? (
        <>
            {goal.images.length > 0 && <ImageCarousel images={goal.images} />}

            <h1>{goal.name}</h1>
            <Typography.Paragraph
                ellipsis={{
                    rows: 3,
                    expandable: true,
                    symbol: 'more',
                }}
                style={{ whiteSpace: 'pre-wrap' }}
            >
                {goal.description}
            </Typography.Paragraph>

            <Descriptions
                bordered
                column={{ xxl: 4, xl: 3, lg: 3, md: 3, sm: 2, xs: 1 }}
            >
                <Item label="Status">{progress.status}</Item>
                <Item label="Worker">
                    {goal.users && <UserButton ids={goal.users} group={true} />}
                </Item>
                <Item label="Owner">
                    <UserButton id={progress.owner} noName={true} />
                </Item>
                <Item label="Supporters">
                    {goal.supporters && (
                        <UserButton ids={goal.supporters} group={true} />
                    )}
                </Item>
                <Item label="Experts">
                    {goal.experts && (
                        <UserButton ids={goal.experts} group={true} />
                    )}
                </Item>
            </Descriptions>
            <Divider orientation="center" type="horizontal">
                Milestones
            </Divider>
            <ActivityTable
                progress={progress}
                account={account}
                state={state}
                setState={setState}
            />
            {account._id === progress.owner && (
                <Button
                    style={{ margin: '0px 20px 50px 0px' }}
                    type="primary"
                    onClick={() => {
                        setState({ ...state, editProgressOpen: true })
                    }}
                >
                    Edit progress
                </Button>
            )}
            {account._id === progress.owner && (
                <Popconfirm
                    title="Create a group if you want to have more similar prgresses with the same people."
                    onConfirm={() =>
                        wsActions.createGroup(account._id, progress._id)
                    }
                >
                    <Button
                        style={{ margin: '0px 20px 50px 0px' }}
                        type="primary"
                    >
                        Create a group
                    </Button>
                </Popconfirm>
            )}
            {account.progresses &&
                account.progresses.includes(progress._id.toString()) && (
                    <Button
                        danger={true}
                        onClick={() => {
                            wsActions.leaveProgress(props)
                        }}
                    >
                        Leave the progress
                    </Button>
                )}

            <Modal
                visible={state.editProgressOpen}
                footer={null}
                onCancel={() => setState({ ...state, editProgressOpen: false })}
                closable={true}
                destroyOnClose={true}
                width="100%"
                style={{ maxWidth: '1000px' }}
            >
                <EditProgress
                    progress={progress}
                    account={account}
                    state={state}
                    setState={setState}
                    friendData={friendData}
                />
            </Modal>
        </>
    ) : null
}

export default withRouter(Activity)
