import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'

import Modal from 'antd/es/modal'
import Button from 'antd/es/button'
import Card from 'antd/es/card'
import Post from '../../components/Post/Post'
import AddPost from '../../components/Post/AddPost'
import Tabs from 'antd/es/tabs'
import AddCard from '../../components/AddCard/AddCard'
import ProgressCard from '../Progresses/ProgressCard'
import AddProgress from '../Progresses/AddProgress'

const Group = props => {
    const [state, setState] = useState({})
    const [startProgressVisible, setStartProgressVisible] = useState(false)

    const groupId = props.history.location.pathname.split('/')[2]
    useEffect(() => {
        props.setData({ currentGroup: groupId })
    }, [props.isAuthed])

    const { group, account, friendData, posts, progressData } = props

    if (!group._id) return null
    props.setData({ currentPosts: group.posts })

    props.setPageTitle({ main: group.name, sub: group.worker })

    const loadedProgresses = group.progresses
        .filter(item => progressData[item])
        .map(item => progressData[item])

    const editGoal = (
        <Modal
            title="Add goal"
            visible={startProgressVisible}
            maskClosable
            closable
            onCancel={() => setStartProgressVisible(false)}
            width="100%"
            style={{ maxWidth: '1000px' }}
            footer={
                <Button
                    key="back"
                    onClick={() => setStartProgressVisible(false)}
                >
                    Close
                </Button>
            }
            destroyOnClose={true}
        >
            <AddProgress
                setStartProgressVisible={setStartProgressVisible}
                account={account}
                friendData={props.friendData}
                inGroup={group._id}
            />
        </Modal>
    )
    return friendData ? (
        <>
            <Tabs>
                <Tabs.TabPane tab="Posts" key="posts">
                    <AddPost
                        parent={groupId}
                        account={account}
                        parentType="progress"
                        // onSend={() => setEditingMessage('')}
                    />
                    {group.posts.map(postId => {
                        if (posts[postId]) {
                            const post = posts[postId]
                            return (
                                <Card>
                                    <Post
                                        friendData={friendData}
                                        account={account}
                                        post={post}
                                    />
                                </Card>
                            )
                        } else return null
                    })}
                </Tabs.TabPane>
                <Tabs.TabPane tab="Progresses" key="progresses">
                    <AddCard
                        title="Start a new progress"
                        sub="Rich your goals or help others!"
                        action={() => {
                            setStartProgressVisible(true)
                        }}
                    />
                    {loadedProgresses.map((progress, index) => (
                        <ProgressCard key={index} progress={progress} />
                    ))}
                    {editGoal}
                </Tabs.TabPane>
            </Tabs>

            <Modal
                visible={state.previewImage}
                footer={null}
                onCancel={() => setState({ ...state, previewImage: '' })}
            >
                <img
                    alt="example"
                    style={{ width: '100%' }}
                    src={state.previewImage}
                />
            </Modal>
        </>
    ) : null
}
const mapStateToProps = state => {
    return {
        isAuthed: state.isAuthed,
        account: state.account,
        posts: state.post,
        group: state.group,
        friendData: state.friendData,
        progressData: state.progressData,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => {
            dispatch(actions.setData(data))
        },
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Group)
)
