import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import Card from 'antd/es/card'
import pullData from '../../utils/pullData'
import Post from '../../components/Post/Post'
import UserButton from '../../components/UserButton/UserButton'
import ImageCarousel from '../../components/UI/ImageCarousel/ImageCarousel'

const Goal = props => {
    const [data, setData] = useState({})
    const { account } = props
    useEffect(() => {
        const pathParts = props.history.location.pathname.split('/')
        if (pathParts.length > 3) {
            const accountId = pathParts[2]
            if (props.isAuthed && account._id === accountId) {
                const goal = account.goals.find(
                    item => item.itemId === pathParts[3]
                )
                setData({ profile: account, goal })
            } else {
                pullData(props, data, setData, { forceAuth: false }, data => {
                    const friendData = {}
                    for (let friend of data.friendData)
                        friendData[friend._id] = friend

                    props.setData({
                        post: { [data.post._id]: data.post },
                        friendData,
                    })
                })
            }
        }
    }, [props.isAuthed])
    if (!data.goal) return null
    const { goal, profile } = data
    props.setData({ currentPosts: goal.post })
    if (goal) {
        props.setPageTitle({ main: goal.name, sub: profile._id })
    }
    return goal ? (
        <Card
            title={
                <span>
                    Goal from <UserButton id={profile._id} /> goals
                </span>
            }
            bordered={false}
        >
            <ImageCarousel images={goal.images} />

            <h1>{goal.name}</h1>
            <p style={{ whiteSpace: 'pre-wrap' }}>{goal.description}</p>
            <Post
                noStartMessage={true}
                post={props.post[goal.post]}
                account={props.account}
                friendData={props.friendData}
                showReplies={true}
            />
        </Card>
    ) : null
}
const mapStateToProps = state => {
    return {
        isAuthed: state.isAuthed,
        account: state.account,
        post: state.post,
        friendData: state.friendData,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => {
            dispatch(actions.setData(data))
        },
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Goal)
)
